/*
 * Desktop Menu Style Mobile
 * STYLE 02
 *
 * 01. Structure
 * 02. Styles
 *
 * id="#site-navigation"
 * & = .menu__main-navigation 
 */


/*
 * local VARs
 */

$_iconic_font_family:       'FontAwesome';      // use here any iconic font and ita value for $_dsk_dropdown_icon
$_dropdown_icon:            '\f0d7';            //fa-caret-down [&#xf0d7;]
$_dropdown_icon_toggled:    '\f00d';
$_mobmenu_width:    320px;


& {
    background-color: #fff;
    position: fixed;
    right: 0;
    top: 0;
    z-index: 100;
    height: 100%;
    width: 100%;
    max-width: 430px;
    text-align: center;
    opacity: 0;
    transition: all 500ms cubic-bezier(0.68, -0.55, 0.265, 1.55);
    transform: scale(0);
    z-index: 10255;

    /*
     * 01. Structure & Behavior
     */

    menu {

        &.nav-menu {
            overflow: auto;
            overflow-x: hidden;
            position: relative;
            display: block;
            vertical-align: middle;

            // common props for nav elems
            li {
                display: block;
                transition: all 400ms 510ms;
                opacity: 0;

                &:nth-child(odd) {
                    transform: translateX(30%);
                }
                &:nth-child(even) {
                    transform: translateX(-30%);
                }
                &:last-child {
                    transform: none;
                }

                &[class*="children"] {
                    @include flexme;

                    & > a {
                        flex: 1;
                    }

                    & > .sub-menu-toggler {
                        width: 44px;

                        &:after {
                            content: $_dropdown_icon;
                            font-family: $_iconic_font_family;
                        }
                    }

                    .sub-menu {
                        display: none;
                        width: 100%;

                        &.opened {
                            display: block;
                        }
                    }
                }
            }
            // end common props for elems

            & > li {
                // 1st level menu - root items
                transform: translateX($_mobmenu_width);
                -ms-transform: translatex(-$_mobmenu_width); // IE fix

            }// 1st lvl end
        } // .nav-menu end
    }


    &.menu-opened {
        opacity: 1;
        transform: scale(1);

        .nav-menu li {
            opacity: 1;
            transform: translateX(0) !important;
        }
    }

    /*
     * 02. Styles Colors, Background, Fonts & Sizes
     */
   menu {

        &.nav-menu {
            text-align: left;

            // common props for elems

            // end common props for elems

            & > li {
                // 1st level menu - root items

                & > a {
                    display: block;
                    padding: 8px 10px;
                }

                &[class*="children"] {

                    & > a {
                        
                    }

                    & > .sub-menu-toggler {
                        text-align: center;
                        
                        &:after {

                        }

                        &.toggled-on {
                            &:after {
                                content: $_dropdown_icon_toggled;
                            }
                        }
                    }

                    & > .sub-menu {
                        // 2nd level menu - sub-menu items
                        padding: 15px;
                        margin: 0;

                        & > li {

                            & > a {

                            }

                            &[class*="children"] {

                                & > a {
                                }

                                & > .sub-menu-toggler {

                                    &:after {

                                    }
                                }

                                & > .sub-menu {
                                    // 3rd level menu - sub-sub-menu items

                                    & > li {

                                        & > a {

                                        }
                                    }
                                } // 3rd lvl sub-menu end
                            } // 3rd li.children end
                        } // li 2nd lvl end
                    } // 2nd sub-menu end
                }// 1st .children end
            }// 1st lvl end
        } // .nav-menu end
    }
}

